import axios from 'axios'

export default class Song {
  constructor ({id, singer, name, album, duration, image}) {
    this.id = id
    this.singer = singer
    this.name = name
    this.album = album
    this.duration = duration
    this.image = image
  }
}

export function createSong (songData) {
  let tempSong = new Song({
    id: songData.id,
    singer: getArtists(songData.ar),
    name: songData.name,
    album: songData.al.name,
    duration: songData.dt,
    image: songData.al.picUrl
  })
  getSongUrl(tempSong.id).then(res => {
    tempSong.url = res
  })
  return tempSong
}

function getArtists (artists) {
  var list = artists.map((value) => {
    return value.name
  })
  return list.join('/')
}

export function getSongUrl (songId) {
  const url = `/api/music/url?id=${songId}`
  return axios.get(url).then((res) => {
    const data = res.data.data
    if (res.status === 200 && res.data.code === 200) {
      if (data) {
        return Promise.resolve(data[0].url)
      }
    }
    return Promise.resolve(`http://music.163.com/song/media/outer/url?id=${songId}.mp3`)
  })
}



