<template>
  <transition name="slide">
    <div class="singer-detail">
      <music-list :songs="songs" :title="title" :bg-img="bgImg"></music-list>
    </div>
  </transition>
</template>

<script>
import {getSingerDetail} from 'api/singer.js'
import {createSong} from 'api/song.js'
import {mapGetters} from 'vuex'
import MusicList from 'components/music-list/music-list'

export default {
  name: 'SingerDetail',
  components: {
    MusicList
  },
  data () {
    return {
      singerDetail: {},
      songs: []
    }
  },
  computed: {
    ...mapGetters([
      'singer'
    ]),
    title () {
      return this.singer.name
    },
    bgImg () {
      return this.singer.img1v1Url
    }
  },
  watch: {
    singerDetail (newSinger) {
      this._processSongs(newSinger)
    }
  },
  created () {
    this._getSingerDetail()
    console.log('Singer', this.singer)
  },
  methods: {
    _getSingerDetail () {
      if (!this.singer.id) {
        this.$router.push('/singer')
        return
      }
      const myParams = {
        id: this.singer.id
      }
      getSingerDetail(myParams)
        .then((res) => {
          if (res.statusText === 'OK' && res.data.code === 200) {
            this.singerDetail = res.data
          }
        })
        // .catch((err) => {
        //   console.log('ERROR', err.message)
        //   this.$router.push('/singer')
        // })
    },
    _processSongs (newSinger) {
      newSinger.hotSongs.forEach((item) => {
        if (item.id) {
          this.songs.push(createSong(item))
        }
      })
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~common/stylus/variables'
  .singer-detail
    position fixed
    top 0
    bottom 0
    left 0
    right 0
    z-index 10

  .slide-enter-active, .slide-leave-active
    transition all 1s
  .slide-enter, .slide-leave-to
    transform translate3d(100%, 0, 0)
</style>



