<template>
  <div class="song-list">
    <ul>
      <li class="song-item"
          v-for="(item, index) in songs"
          :key="index"
          @click="selectSong(item, index)"
      >
        <div class="song-name">{{item.name}}</div>
        <p class="song-desc">{{getSongDesc(item)}}</p>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'SongList',
  props: {
    songs: {
      type: Array,
      default: null
    }
  },
  methods: {
    getSongDesc (song) {
      return song.singer + '  . [' + song.album + ']'
    },
    selectSong (song, index) {
      this.$emit('selectSong', song, index)
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~common/stylus/variables'
  .song-list
    .song-item
      padding 1rem 2.2rem
      .song-name
        font-size $font-size-medium
        line-height 2rem
        color $color-text-b
      .song-desc
        font-size $font-size-small
        color $color-text-f
</style>



