import axios from 'axios'

export function getSinger (inputParms) {
  const url = '/api/artist/list'

  return axios.get(url, {
    params: inputParms
  }).then((res) => {
    return Promise.resolve(res)
  })
}

export function getTopSinger (inputParms) {
  const url = '/api/top/artists'

  return axios.get(url, {
    params: inputParms
  }).then((res) => {
    return Promise.resolve(res)
  })
}

export function getSingerDetail (inputParms) {
  const url = '/api/artists'
  return axios.get(url, {
    params: inputParms
  }).then((res) => {
    return Promise.resolve(res)
  })
}



