<template>
  <div class="music-list">
    <div class="header">
      <div class="iconfont header-back" @click="back">&#xe624;</div>
      <div class="header-title">{{title}}</div>
      <div class="header-right"></div>
    </div>
    <div class="singer-image" :style="bgImgStyle" ref="bgImg">
      <div class="play-btn">
        <div class="btn-wrapper"  v-show="songs.length > 0 && showBtn">
          <span class="iconfont play">&#xe658;</span>
          <span class="play-desc">随机播放所有歌曲</span>
        </div>
      </div>
    </div>
    <div class="bg-layer" ref="layer"></div>
    <scroll class="song-list-container"
            @scroll="scrollBgImg"
            :probe-type="probeType"
            :listen-scroll="listenScroll"
            ref="list"
    >
      <div class="song-list-wrapper">
        <song-list @selectSong="_selectPlaylist" :songs="songs"></song-list>
      </div>
      <div class="loading-container" v-show="!songs.length">
        <loading></loading>
      </div>
    </scroll>
  </div>
</template>

<script>
import Scroll from 'base/scroll/scroll'
import Loading from 'base/loading/loading'
import SongList from 'base/song-list/song-list'
import {prefixStyle} from 'common/js/dom'
import {mapActions} from 'vuex'

const RESERVE_HEIGHT = 35
const transformName = prefixStyle('transform')

export default {
  name: 'MusicList',
  components: {
    Scroll,
    SongList,
    Loading
  },
  props: {
    songs: {
      type: Array,
      default: null
    },
    bgImg: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      scrollY: 0,
      showBtn: true
    }
  },
  methods: {
    scrollBgImg (pos) {
      this.scrollY = pos.y
    },
    back () {
      this.$router.back()
    },
    _selectPlaylist (song, index) {
      // 选中歌曲后，更新播放列表，更新当前歌曲
      // console.log(this.songs.length)
      this.selectPlaylist({
        list: this.songs,
        index: index
      })
    },
    ...mapActions([
      'selectPlaylist'
    ])
  },
  watch: {
    scrollY (newY) {
      let tranY = Math.max(this.minImgTranslateY, newY)
      let bgZIndex = 0
      let scale = 1
      let percent = Math.abs(newY / this.bgImgHeight)

      let bgImg = this.$refs.bgImg
      let layer = this.$refs.layer

      if (newY > 0) {
        scale += percent
      }
      if (newY < this.minImgTranslateY) {
        this.showBtn = false
        bgZIndex = 40
        bgImg.style.paddingTop = 0
        bgImg.style.height = `${RESERVE_HEIGHT}px`
      } else {
        this.showBtn = true
        bgZIndex = 0
        bgImg.style.paddingTop = '70%'
        bgImg.style.height = '0px'
      }
      bgImg.style.zIndex = bgZIndex
      bgImg.style[transformName] = `scale(${scale})`
      layer.style[transformName] = `translate3d(0, ${tranY}px, 0)`
    }
  },
  created () {
    this.probeType = 3
    this.listenScroll = true
  },
  computed: {
    bgImgStyle () {
      return `background-image: url(${this.bgImg})`
    }
  },
  mounted () {
    this.bgImgHeight = this.$refs.bgImg.clientHeight
    this.minImgTranslateY = -this.bgImgHeight + RESERVE_HEIGHT
    this.$refs.list.$el.style.top = `${this.bgImgHeight}px`
  }
}
</script>

<style lang="stylus" scoped>
  @import '~common/stylus/variables'
  .music-list
    position fixed
    top 0
    bottom 0
    left 0
    right 0
    color $color-text-b
    font-size $font-size-medium-x
    background-color $color-background-f
    .header
      position absolute
      width 100%
      display flex
      text-align center
      z-index 50
      .header-back
        flex 0 0 35px
        width 35px
        height 35px
        // font-size $font-size-medium-x
        line-height 35px
      .header-title
        flex 1
        text-align center
        // font-size $font-size-medium-x
        line-height 35px
      .header-right
        flex 0 0 35px
    .singer-image
      position relative
      top 0
      width 100%
      background-size cover
      padding-top 70%
      transform-origin top
      // z-index 30
      // opacity 0.5
      .play-btn
        display flex
        justify-content center
        position absolute
        bottom 20px
        width 100%
        font-size $font-size-medium
        z-index 30
        .btn-wrapper
          border: 1px solid $color-text
          border-radius 10px
          .play
            flex 0 0 30px
            line-height 30px
          .play-desc
            flex 0 0 160px
            line-height 30px
            color $color-text
    .bg-layer
      position relative
      height 100%
      background-color $color-background-f
      z-index 20
    .song-list-container
      position fixed
      top 0
      bottom 0
      left 0
      right 0
      z-index 30
</style>



