<template>
  <div class="loading">
    <span><img src="./loading2.gif" alt=""></span>
    <p>{{ title }}</p>
  </div>
</template>

<script>
export default {
  name: 'Loading',
  props: {
    title: {
      type: String,
      default: '正在加载...'
    }
  }
}
</script>

<style lang="stylus" scoped>
// @import from '~common/stylus/variables.styl'
  .loading
    text-align center
    img
      width 50%
</style>



