<template>
  <div class="singer">
    <listview @select="selectSinger" :listdata="topSingerAlpha"></listview>
    <router-view></router-view>
  </div>
</template>

<script>
import pinyin from 'js-pinyin'
import Scroll from 'base/scroll/scroll'
import Listview from 'base/listview/listview'
import {getSinger, getTopSinger} from 'api/singer'
import {mapMutations} from 'vuex'

export default {
  name: 'Singer',
  components: {
    Listview,
    Scroll
  },
  data () {
    return {
      topSinger: [],
      topSingerAlpha: []
    }
  },
  methods: {
    _getSinger () {
      const myParms = {
        cat: 5001,
        limit: 30
        // offset: 0
        // initial:
      }
      getSinger(myParms).then((res) => {
        // console.log('Get all singers', res)
      })
    },
    // 获取热门歌手
    _getTopSinger () {
      const myParms = {
        limit: 99,
        offset: 0
      }
      getTopSinger(myParms).then((res) => {
        if (res.statusText === 'OK') {
          this.topSinger = res.data.artists
          this._getSingerPinyin()
          this._processData()
        }
      })
    },
    // 点击触发进入歌手页面
    selectSinger (singer) {
      this.$router.push({
        path: `/singer/${singer.id}`
      })
      this.setSinger(singer)
    },
    // 歌手名称汉语转拼音
    _getSingerPinyin () {
      this.topSinger.forEach((item, idx) => {
        item.pinyin = pinyin.getFullChars(item.name)
      })
    },
    // 将获取到的歌手数据按照字母序聚类重排序
    _processData () {
      this._getSingerPinyin()
      let map = {}
      this.topSinger.forEach((item, idx) => {
        const key = item.pinyin.charAt(0).toUpperCase()
        if (!map[key]) {
          map[key] = {
            title: key,
            items: []
          }
        }
        map[key].items.push(item)
      })
      for (let key in map) {
        let val = map[key]
        if (val.title.match(/[a-zA-Z]/)) {
          this.topSingerAlpha.push(val)
        }
      }
      this.topSingerAlpha.sort((a, b) => {
        return a.title.charCodeAt(0) - b.title.charCodeAt(0)
      })
    },
    ...mapMutations({
      setSinger: 'SET_SINGER'
    })
  },
  created () {
    setTimeout(() => {
      // this._getSinger()
      this._getTopSinger()
    }, 1000)
  }
}
</script>

<style lang="stylus" scoped>
  @import '~common/stylus/variables'
  .singer
    position fixed
    top 80px
    bottom 0
    width 100%
</style>



