<template>
  <div class="listview">
    <scroll class="list-container"
            ref="listcontainer"
            :listenScroll="listenScroll"
            :probeType="probeType"
            @scroll="scroll"
    >
      <ul class="list">
        <li class="list-group"
            ref="listgroup"
            v-for="(group, idx) of listdata"
            :key="idx"
        >
          <p class="list-subtitle">{{group.title}}</p>
          <div class="item-wrap" @click="selectItem(item)" v-for="(item, itemIdx) of group.items" :key="itemIdx">
            <div class="item-image">
              <img v-lazy="item.img1v1Url" alt="img">
            </div>
            <div class="item-name">{{item.name}}</div>
            <div class="item-album">[专辑数: {{item.albumSize? item.albumSize:0}}]</div>
          </div>
        </li>
      </ul>
      <div class="shortcut"
          ref="shortcut"
          @touchstart="onShortcutTouchStart"
          @touchmove="onShortcutTouchMove"
      >
        <ul>
          <li class="item"
              v-for="(item, index) in shortcutList"
              :class="{'current': currentIndex === index}"
              :key="index"
              :data-index="index"
          >
            {{item}}
          </li>
        </ul>
      </div>
      <div class="list-fixed-title" v-show="fixedTitle" ref="fixedTitle">
        <p class="list-subtitle">{{fixedTitle}}</p>
      </div>
    </scroll>
    <div class="loading-container" v-show="!listdata.length">
      <loading></loading>
    </div>
  </div>
</template>

<script>
import Scroll from 'base/scroll/scroll'
import Loading from 'base/loading/loading'
import {getEleAttr} from 'common/js/dom.js'

const ANCHOR_HEIGHT = 18
const TITLE_HEIGHT = 25

export default {
  name: 'Listview',
  components: {
    Scroll,
    Loading
  },
  created () {
    this.touch = {}
    this.listenScroll = true
    this.probeType = 3
  },
  data () {
    return {
      scrollY: -1,
      currentIndex: 0,
      diff: 0
    }
  },
  props: {
    listdata: {
      type: Array,
      default: null
    }
  },
  computed: {
    // 侧边栏入口列表
    shortcutList () {
      return this.listdata.map((group) => {
        return group.title.substr(0, 1)
      })
    },
    // 固定栏
    fixedTitle () {
      if (this.scrollY > 0) {
        return ''
      }
      return this.shortcutList[this.currentIndex] ? this.shortcutList[this.currentIndex] : ''
    }
  },
  methods: {
    selectItem (item) {
      this.$emit('select', item)
    },
    onShortcutTouchStart (e) {
      // 获取触摸锚点元素的索引
      let anchorIndex = getEleAttr(e.target, 'index')
      let firstTouch = e.touches[0]
      // 记录下触摸起点的信息：锚点位置，锚点索引
      this.touch.y1 = firstTouch.pageY
      this.touch.anchorIndex = anchorIndex
      this._scrollToEle(anchorIndex)
    },
    onShortcutTouchMove (e) {
      let firstTouch = e.touches[0]
      this.touch.y2 = firstTouch.pageY
      // 计算出触点移动偏移的锚点个数
      let delta = Math.floor((this.touch.y2 - this.touch.y1) / ANCHOR_HEIGHT)
      let anchorIndex = parseInt(this.touch.anchorIndex) + delta
      this._scrollToEle(anchorIndex)
    },
    scroll (pos) {
      // 滚动事件触发，更新滚动位置scrollY
      this.scrollY = pos.y
    },
    _scrollToEle (index) {
      // 更新scrollY，触发当前锚点值更新
      this.scrollY = -this.listHeight[index]
      this.$refs.listcontainer.scrollToElement(this.$refs.listgroup[index], 0)
    },
    _calculateHeight () {
      // 计算列表元素高度
      this.listHeight = []
      const list = this.$refs.listgroup
      let height = 0
      this.listHeight.push(height)
      for (let i = 0; i < list.length; i++) {
        let item = list[i]
        height += item.clientHeight
        this.listHeight.push(height)
      }
    }
  },
  watch: {
    listdata () {
      setTimeout(() => {
        // 列表数据变化时，重新计算列表组的高度
        this._calculateHeight()
      }, 20)
    },
    // 页面的当前锚点统一由scrollY计算
    scrollY (newY) {
      // newY为顶部偏离初始位置的高度（相对于初始位置，为负值）
      const listHeight = this.listHeight
      for (let i = 0; i < listHeight.length; i++) {
        let height1 = listHeight[i]
        let height2 = listHeight[i + 1]
        // 滚动到顶部
        if (-newY < 0) {
          this.currentIndex = 0
          break
        }
        // 滚动到底部
        if (!height2 && -newY > height1) {
          this.currentIndex = listHeight.length - 2
          break
        }
        // 滚动到中间部分
        if (-newY >= height1 && -newY < height2) {
          this.currentIndex = i
          this.diff = height2 + newY
          break
        }
      }
    },
    diff (newVal) {
      let fixedTop = (newVal > 0 && newVal < TITLE_HEIGHT) ? (newVal - TITLE_HEIGHT) : 0
      if (this.fixedTop === fixedTop) {
        return
      }
      this.fixedTop = fixedTop
      this.$refs.fixedTitle.style.transform = `translate3d(0, ${this.fixedTop}px, 0)`
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~common/stylus/variables'
  .listview
    height 100%
    .list-container
      height 100%
      overflow hidden
      .list
        // padding-top 10px
        // padding-bottom 10px
        .list-group
          height 100%
          .list-subtitle
            padding: 0px 10px
            background-color $background-color-d
            font-size $font-size-medium
            line-height 25px
            color #000000
          .item-wrap
            display flex
            margin 15px
            .item-image
              flex 0 0 60px
              height 60px
              img
                width 100%
                border-radius 50%
            .item-name
              flex 1
              line-height 60px
              color black
              font-size $font-size-medium-x
              padding-left 15px
            .item-album
              flex 1
              color black
              line-height 60px
              font-size $font-size-medium
              font-weight lighter
      .shortcut
        position absolute
        top 15%
        right 2px
        .item
          width 18px
          height 18px
          font-size $font-size-medium
          line-height 18px
          background-color $color-background-d
          text-align center
        .current
          color $color-theme-e
      .list-fixed-title
        position absolute
        top 0
        width 100%
        .list-subtitle
          padding: 0px 10px
          background-color $background-color-d
          font-size $font-size-medium
          line-height 25px
          color #000000
    .loading-container
      position absolute
      top 25%
      width 100%
</style>



