<template>
  <div class="slider" ref="slider">
    <div class="slider-group" ref="sliderGroup">
      <slot></slot>
    </div>
    <div class="dots">
      <span class="dot" v-for="(item, index) in dots" :key="index" :class="{active: curPage === index}"></span>
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
import {addClass} from 'common/js/dom'

export default {
  name: 'Slider',
  data () {
    return {
      dots: [],
      curPage: 0
    }
  },
  props: {
    loop: {
      type: Boolean,
      default: false
    },
    autoPlay: {
      type: Boolean,
      default: false
    },
    interval: {
      type: Number,
      default: 4000
    }
  },
  mounted () {
    setTimeout(() => {
      this._setSliderWidth()
      this._initDots()
      this._initSlider()
      if (this.autoPlay) {
        this._play()
      }
    }, 20)

    window.addEventListener('resize', () => {
      if (this.slider) {
        this._setSliderWidth(true)
        this.slider.refresh()
      }
    })
  },
  destroyed () {
    clearTimeout(this.timer)
  },
  methods: {
    _setSliderWidth (isResize) {
      this.children = this.$refs.sliderGroup.children
      let width = 0
      let sliderWidth = this.$refs.slider.clientWidth

      for (let i = 0; i < this.children.length; i++) {
        let child = this.children[i]
        addClass(child, 'slider-item')
        child.style.width = sliderWidth + 'px'
        width += sliderWidth
      }
      if (this.loop && !isResize) {
        width += 2 * sliderWidth
      }
      this.$refs.sliderGroup.style.width = width + 'px'
    },
    _initSlider () {
      this.slider = new BScroll(this.$refs.slider, {
        scrollX: true,
        scrollY: false,
        momentum: false,
        snap: true,
        snapLoop: this.loop,
        snapThreshold: 0.3,
        snapSpeed: 400
      })
      this.slider.on('scrollEnd', () => {
        let pageIndex = this.slider.getCurrentPage().pageX
        if (this.loop) {
          pageIndex -= 1
        }
        this.curPage = pageIndex

        if (this.autoPlay) {
          clearTimeout(this.timer)
          this._play()
        }
      })
    },
    _initDots () {
      this.dots = new Array(this.children.length)
    },
    _play () {
      let nextPageIdx = this.curPage + 1
      if (this.loop) {
        nextPageIdx += 1
      }
      this.timer = setTimeout(() => {
        this.slider.goToPage(nextPageIdx, 0, 400)
      }, this.interval)
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~common/stylus/variables'
  .slider
    // height 100%
    overflow hidden
    .slider-group
      position relative
      overflow hidden
      white-space nowrap
      .slider-item
        float left
        overflow hidden
        a
          img
            width 100%
    .dots
      position relative
      bottom 14px
      text-align center
      .dot
        display inline-block
        width 8px
        height 8px
        background-color white
        border-radius 50%
        margin 0 4px
        &.active
          background-color red
</style>



