<template>
  <div class="recommend">
    <scroll ref="scroll" class="recommend-content" :data="playlists">
      <div>
        <div class="slider-wrapper" v-if="recSlider.length">
          <slider>
            <div v-for="(item, index) in recSlider" :key="index">
              <a :href="item.linkUrl">
                <img :src="item.picUrl" alt="rec-img" @load="loadImage">
              </a>
            </div>
          </slider>
        </div>
        <div class="recommend-list">
          <h1>热门歌单推荐</h1>
          <ul class="song-list">
            <li class="item" v-for="(item, index) of playlists" :key="index">
              <div class="item-album">
                <img v-lazy="item.coverImgUrl" alt="playlist-img">
              </div>
              <div class="item-detail">
                <div class="descrip">{{item.description}}</div>
                <div class="author">by <span>{{item.creator.nickname}}</span></div>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </scroll>
    <div class="loading-container" v-show="!playlists.length">
      <loading></loading>
    </div>
  </div>
</template>

<script>
import Scroll from 'base/scroll/scroll'
import Slider from 'base/slider/slider'
import {getRecommend, getQQDiscList, getNeteaseMusicPlaylist} from 'api/recommend'
import {ERR_OK} from 'api/config'
import Loading from 'base/loading/loading'

export default {
  name: 'Recommend',
  data () {
    return {
      recSlider: [],
      songList: [],
      recList: {},
      playlists: []
    }
  },
  components: {
    Slider,
    Scroll,
    Loading
  },
  methods: {
    // 获取轮播图和歌曲列表
    _getRecommend () {
      getRecommend().then((res) => {
        if (res.code === ERR_OK) {
          this.recSlider = res.data.slider
          this.songList = res.data.songList
        }
      })
    },
    // 获取qq音乐推荐歌单
    _getQQDiscList () {
      getQQDiscList().then((res) => {
        // console.log('get qq disclist', res)
      })
    },
    // 获取网易云推荐歌单
    _getNeteaseMusicPlaylist () {
      getNeteaseMusicPlaylist().then((res) => {
        if (res.statusText === 'OK') {
          this.playlists = res.data.playlists
          // console.log('playlists', this.playlists)
        }
      })
    },
    // 图片载入完成刷新scroll组件
    loadImage () {
      if (!this.checkedLoaded) {
        this.checkedLoaded = true
        this.$refs.scroll.refresh()
      }
    }
  },
  created () {
    this._getRecommend()
    // this._getQQDiscList()
    setTimeout(() => {
      this._getNeteaseMusicPlaylist()
    }, 1000)
  }
}
</script>

<style lang="stylus" scoped>
  @import '~common/stylus/variables'
  .recommend
    position fixed
    width 100%
    top 80px
    bottom 0
    // .slider-wrapper
      // overflow hidden
    .recommend-content
      height 100%
      overflow hidden
      .recommend-list
        h1
          color $color-text-d
          text-align center
          font-size $font-size-medium-x
          line-height 4rem
        .song-list
          .item
            display flex
            padding-left 1.2rem
            margin-bottom 0.8rem
            .item-album
              flex 0 0 60px
              width 60px
              height 60px
              img
                width 100%
            .item-detail
              flex 1
              display flex
              flex-direction column
              margin-left 1rem
              overflow hidden
              .descrip
                flex 1
                color $color-text-b
                font-size $font-size-medium-x
                white-space nowrap
              .author
                flex 1
                color $color-text-f
                font-size $font-size-small
                line-height 16px
                span
                  color #273746
                  font-size $font-size-medium
    .loading-container
      position absolute
      width 100%
      top 45%
</style>



