<template>
  <div class="tabs">
    <router-link to="/recommend" tag="div" class="tab-item">
      <span class="tab-link">推荐</span>
    </router-link>
    <router-link to="/singer" tag="div" class="tab-item">
      <span class="tab-link">歌手</span>
    </router-link>
    <router-link to="/list" tag="div" class="tab-item">
      <span class="tab-link">歌单</span>
    </router-link>
    <router-link to="/search" tag="div" class="tab-item">
      <span class="tab-link">搜索</span>
    </router-link>
  </div>
</template>

<script>
export default {
  name: 'Tab'
}
</script>

<style lang="stylus" scoped>
  @import '~common/stylus/variables'
  .tabs
    display flex
    justify-content space-around
    // line-height 1.5rem
    font-size $font-size-medium
    margin 10px 10px 0x 10px
    padding 5px 10px
    color $color-text
    .tab-item
      flex  1
      text-align center
      .tab-link
        line-height 3rem
        padding-bottom 2px
      &.router-link-active
        .tab-link
          color $color-theme-e
          border-bottom 2px solid $color-theme-e
</style>



