import myjsonp from 'common/js/jsonp'
import {commonParams, options} from './config'
import axios from 'axios'

export function getRecommend () {
  const url = 'https://c.y.qq.com/musichall/fcgi-bin/fcg_yqqhomepagerecommend.fcg'
  const data = Object.assign({}, commonParams, {
    g_tk: 5381,
    platform: 'h5',
    uin: 0,
    needNewCode: 1
  })
  return myjsonp(url, data, options)
}

export function getQQDiscList () {
  const url = '/api/getQQDiscList'
  // const url = 'https://c.y.qq.com/splcloud/fcgi-bin/fcg_get_diss_by_tag.fcg'
  const data = Object.assign({}, {
    inCharset: 'utf-8',
    outCharset: 'utf-8',
    notice: 0,
    picmid: 1,
    g_tk: 5381,
    rnd: Math.random(),
    loginUin: 0,
    hostUin: 0,
    platform: 'yqq',
    needNewCode: 0,
    categoryId: 10000000,
    sortId: 5,
    sin: 0,
    ein: 29
  })

  return axios.get(url, {
    params: data
  }).then((res) => {
    console.log(res.data)
    return Promise.resolve(res.data)
  })
}

export function getQQSongList () {
  const data = {
    g_tk: 5381,
    uin: 0,
    inCharset: 'utf-8',
    outCharset: 'utf-8',
    notice: 0,
    platform: 'h5',
    needNewCode: 1,
    tpl: 3,
    page: 'detail',
    type: 'top',
    topid: 27,
    _: '1519963122923'
  }

  return axios.get('/getDiscList/v8/fcg-bin/fcg_v8_toplist_cp.fcg', {
    params: data
  }).then((res) => {
    console.log(res.data)
  })
}

export function getNeteaseMusicPlaylist () {
  const url = '/api/top/playlist'
  const myParams = {
    limit: 100,
    order: 'hot'
  }
  return axios.get(url, {
    params: myParams
  }).then((res) => {
    return Promise.resolve(res)
  })
}



