<template>
  <div class="player" v-show="playlist.length > 0">
    <!-- 全屏播放器界面 -->
    <transition name="normal">
      <div class="player-normal" v-show="fullScreen">
        <!-- 全屏播放界面——背景图层 -->
        <div class="bg-layer">
          <img :src="currentSong.image" alt="背景图">
        </div>
        <!-- 全屏播放界面——顶部 -->
        <div class="top">
          <div class="back" @click="back">
            <i class="iconfont">&#xe624;</i>
          </div>
          <h2 class="song-name">{{currentSong.name}}</h2>
          <p class="song-artist">{{currentSong.singer}}</p>
        </div>
        <!-- 全屏播放界面——中部 -->
        <div class="middle">
          <div class="album-image">
            <img :src="currentSong.image" alt="封面">
          </div>
        </div>
        <!-- 全屏播放界面——底部按钮栏 -->
        <div class="bottom">
          <!-- 播放时间、进度栏 -->
          <div class="time-wrapper">
            <div class="time-current">{{formatTime(currentTime)}}</div>
            <div class="time-line">
              <progress-bar :percent="percent"></progress-bar>
            </div>
            <div class="time-duration">{{formatTime(currentSong.duration / 1000)}}</div>
          </div>
          <!-- 底部按钮 -->
          <div class="btn-wrapper">
            <span class="iconfont btn play-mode">&#xe656;</span>
            <span class="iconfont btn play-last" @click="prev">&#xe653;</span>
            <span class="iconfont btn play-btn" :class="disableClass" @click="togglePlay">
              {{playing ? "&#xe654;" : "&#xe64f;"}}
            </span>
            <span class="iconfont btn play-next" @click="next">&#xe651;</span>
            <span class="iconfont btn like">&#xe660;</span>
          </div>
        </div>
      </div>
    </transition>
    <!-- 迷你播放器界面 -->
    <transition name="mini">
      <div class="player-mini" v-show="!fullScreen" @click="open">
        <div class="album-image">
          <img :src="currentSong.image" alt="封面">
        </div>
        <div class="song-detail">
          <h3 class="song-name">{{currentSong.name}}</h3>
          <p class="song-artist">{{currentSong.singer}}</p>
        </div>
        <div class="control-btns">
          <i class="iconfont play" @click="miniTogglePlay">
            {{playing ? "&#xe654;" : "&#xe64f;"}}
          </i>
          <i class="iconfont playlist" @click="showPlaylist">&#xe643;</i>
        </div>
      </div>
    </transition>
    <!-- audio媒体播放 -->
    <audio ref="audio"
          :src="currentSong.url"
          @canplay="ready"
          @timeupdate="timeChange"
          @error="errorOccur"
    ></audio>
  </div>
</template>

<script>
import ProgressBar from 'base/progress-bar/progress-bar'
import {mapGetters, mapMutations} from 'vuex'
// import {getSongUrl} from 'api/song'

export default {
  name: 'Player',
  components: {
    ProgressBar
  },
  data () {
    return {
      readyState: false,
      currentTime: 0,
      percent: 0
    }
  },
  computed: {
    ...mapGetters([
      'fullScreen',
      'playlist',
      'currentIndex',
      'currentSong',
      'playing'
    ]),
    bgStyle () {
      return {
        background: `url(${this.currentSong.image})`
      }
    },
    disableClass () {
      return this.readyState ? '' : 'disable'
    }
  },
  methods: {
    back () {
      this.setFullScreen(false)
    },
    open () {
      this.setFullScreen(true)
    },
    togglePlay () {
      if (!this.readyState) return
      this.setPlaying(!this.playing)
    },
    miniTogglePlay (event) {
      event.stopPropagation()
      this.togglePlay()
    },
    errorOccur (event) {
      console.log(event)
    },
    ready () {
      this.readyState = true
    },
    showPlaylist () {
      event.stopPropagation()
      console.log(this.playlist)
    },
    prev () {
      // if (!this.readyState) return
      let newIndex = this.currentIndex - 1
      if (newIndex < 0) {
        newIndex = this.playlist.length - 1
      }
      this.setCurrentIndex(newIndex)
      if (!this.playing) {
        this.togglePlay()
      }
      this.readyState = false
    },
    next () {
      // if (!this.readyState) return
      let newIndex = this.currentIndex + 1
      if (newIndex > this.playlist.length - 1) {
        newIndex = 0
      }
      this.setCurrentIndex(newIndex)
      if (!this.playing) {
        this.togglePlay()
      }
      this.readyState = false
    },
    // 播放时间变化触发函数
    timeChange (event) {
      const duration = this.currentSong.duration
      this.currentTime = event.target.currentTime
      this.percent = parseInt(this.currentTime * 1000) / (duration)
      // console.log(duration, this.currentTime * 1000, this.percent)
    },
    formatTime (time) {
      time = parseInt(time)
      const minute = parseInt(time / 60)
      const second = parseInt(time % 60)
      return `${this._pad(minute)}:${this._pad(second)}`
    },
    _pad (num, len = 2) {
      let currentLen = num.toString().length
      while (currentLen < len) {
        num = '0' + num
        currentLen++
      }
      return num
    },
    ...mapMutations({
      setFullScreen: 'SET_FULL_SCREEN',
      setPlaying: 'SET_PLAYING_STATE',
      setCurrentIndex: 'SET_CURRENT_INDEX'
    })
  },
  watch: {
    currentSong (newVal) {
      const audio = this.$refs.audio
      this.$nextTick(() => {
        audio.play()
      })
    },
    playing (newPlaying) {
      const audio = this.$refs.audio
      this.$nextTick(() => {
        newPlaying ? audio.play() : audio.pause()
      })
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~common/stylus/variables'
  .player
    color $color-text-b
    .player-normal
      position fixed
      top 0
      bottom 0
      left 0
      right 0
      text-align center
      width 100%
      height 100%
      background-color $color-background-f
      &.normal-enter-active, &.normal-leave-active
        transition all 0.5s
      &.normal-enter, &.normal-leave-to
        opacity 0
        transform translate3d(0, -100%, 0)
        // &.top
        //   transform translate3d(0, -100%, 0)
        // &.bottom
        //   transform translate3d(0, 100%, 0)
      .bg-layer
        position absolute
        top 0
        bottom 0
        left 0
        right 0
        height 100%
        z-index -1
        img
          width 100%
          height 100%
          opacity 0.3
      .top
        position relative
        .back
          position absolute
          left 10px
          width 30px
          height 30px
          line-height 30px
          font-size $font-size-medium-x
          font-weight bolder
          color $color-text-b
          transform rotate(-90deg)
        .song-name
          font-size $font-size-medium-x
          line-height 35px
          margin 0 40px
          white-space nowrap
          overflow hidden
        .song-artist
          font-size $font-size-medium
          margin 0 70px
          line-height 20px
          white-space nowrap
          overflow hidden
      .middle
        // position
        // overflow hidden
        margin-top 25%
        .album-image
          margin 0 auto
          height 240px
          width 240px
          img
            display inline-block
            border 30px solid rgba(0, 0, 0, 0.8)
            border-radius 50%
            height 180px
            width 180px
      .bottom
        position absolute
        bottom 0
        left 0
        right 0
        background-color lightblue
        .time-wrapper
          font-size $font-size-small
          line-height 40px
          display flex
          & > div
            display inline-block
            text-align center
          .time-current
            flex 0 0 50px
          .time-line
            flex 1
          .time-duration
            flex 0 0 50px
        .btn-wrapper
          display flex
          align-items center
          padding 0px 10px
          .btn
            font-size $btn-size-medium
            flex 1
            line-height 68px
            &.play-btn
              flex 1.6
              font-size $btn-size-medium-x
            &.play-mode, &.like
              font-size $btn-size-medium-s
            &.disable
              color $color-text-c
    .player-mini
      position fixed
      display flex
      align-items center
      bottom 0px
      left 0
      right 0
      height 49px
      padding 0 20px
      opacity 0.9
      text-align center
      background-color $color-background-b
      &.mini-enter-active, &.mini-leave-active
        transition all 0.3s
      &.mini-enter, &.mini-leave-to
        opacity 0
        transform translate3d(0, 100%, 0)
      .album-image
        flex 0 0 42px
        font-size 0
        img
          display inline-block
          width 42px
          height 42px
          border-radius 50%
          border 1px solid black
      .song-detail
        flex 1
        text-align left
        padding 0.8rem 2rem
        white-space nowrap
        overflow hidden
        .song-name
          font-size $font-size-medium
        .song-artist
          font-size $font-size-small
          color $color-text-f
      .control-btns
        display flex
        flex 0 0 90px
        line-height 45px
        & .iconfont
          flex 1
          font-size $btn-size-small
</style>



