<template>
  <div class="header">
      <div class="header-icon"></div>
      <div class="header-title">Happy Music</div>
  </div>
</template>

<script>
export default {
  name: 'XHeader'
}
</script>

<style lang="stylus" scoped>
  @import '~common/stylus/variables'
  @import '~common/stylus/mixin'
  .header
    display flex
    justify-content center
    align-items center
    font-size 0
    line-height 4rem
    margin 0 auto
    background-color $color-background-d
    .header-icon
      font-size $font-size-small
      width 2rem
      height 2rem
      bgImg('logo', 2)
    .header-title
      font-size $font-size-medium-x
      color $color-text
</style>



