<template>
  <div class="progress-bar"  ref="progressBar">
    <div class="progress-bar-inner">
      <div class="progress-line" ref="progressLine"></div>
      <div class="progress-btn-wrapper">
        <div class="progress-btn" ref="progressBtn"></div>
      </div>
    </div>
  </div>
</template>

<script>
import {prefixStyle} from 'common/js/dom'

const transform = prefixStyle('transform')
const BUTTON_OFFSET = 7.5

export default {
  name: 'ProgressBar',
  props: {
    percent: {
      type: Number,
      default: 0
    }
  },
  watch: {
    percent (newPercent) {
      if (newPercent >= 0) {
        const barWidth = this.$refs.progressBar.clientWidth
        const progressWidth = parseInt(barWidth * this.percent)
        const btnPosition = progressWidth - BUTTON_OFFSET > 0 ? progressWidth - BUTTON_OFFSET : 0
        this.$refs.progressLine.style.width = `${progressWidth}px`
        this.$refs.progressBtn.style[transform] = `translate3d(${btnPosition}px, 0, 0)`
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~common/stylus/variables'
  .progress-bar
    width 100%
    height 40px
    display flex
    align-items center
    .progress-bar-inner
      position relative
      // display inline-block
      height 5px
      width 100%
      // margin-top 8px
      background-color $progress-bg-color
      border-radius 5px
      .progress-line
        height 100%
        background-color bisque
      .progress-btn-wrapper
        position absolute
        top -4px
        .progress-btn
          width 15px
          height 15px
          border-radius 50%
          background-color #ffffff
</style>



